

.container {
    padding: 20px;
    max-width: 800px;
    margin: 0 auto;
}

.description {
    text-align: center;
    padding-bottom: 20px;
    border-bottom: 3px solid rgb(210 215 220);
    display: flex;
    align-items: center;
    justify-content: center;
    margin: 0px 30px;
    margin-bottom: 30px;
}

.detail {
    padding: 10px; 
    box-sizing: border-box;
}

.slick-slide {
    box-sizing: border-box;
    padding-top: 20px;
}

.slick-list {
    margin: 0 -10px;
}

.slick-dots {
    position: relative;
    bottom: -20px;
}

.slick-dots li button::before {
    font-size: 10px;
}

li.slick-active button::before {
    font-size: 15px;
}

.rating-box p {
    display: flex;
    align-items: center;
    font-size: 18px;
    font-weight: bold;
    color: #fff;
}

.slick-arrow {
    z-index: 2;
    background: rgb(255 255 255 / 0%) !important;
}

.slick-prev {
    display: none;
    left: 0; 
    margin: 0;
}

.slick-next {
    right: 0 !important;
}

.item-box {
    display: flex;
    flex-direction: column; 
    align-items: center;
    margin-bottom: 20px;
    border-radius: 5px;
    height: 400px;
    width: 100%;
    max-width: 100%;
    overflow: hidden;
    margin: 0 auto;
    position: relative;
    background: #fff;
}

.item-box-image {
    width: 100%;
    height: 300px;
    display: flex;
    align-items: center;
    justify-content: center;
    position: relative;
    overflow: hidden;
}

.estabelecimento-imagem {
    width: 100%;
    height: 100%;
    object-fit: cover;
    box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.1);
}

.rating-box {
    margin: 20px;
    position: absolute;
    top: -10px;
    background-color: #f7c325;
    color: #fff;
    border-radius: 50px;
    padding: 10px;
    font-size: 18px;
    font-weight: bold;
    width: 80px;
    height: 30px;
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 1;
}

.item-box-info {
    width: 100%;
    text-align: left;
    padding: 10px;
    box-sizing: border-box;
    flex-grow: 1;
    display: flex;
    flex-direction: column;
    align-items: flex-start !important;
    justify-content: space-around;
}

.nome {
    font-size: 24px;
    font-weight: bold;
    color: #333;
    margin-bottom: 5px;
}

.price-status {
    display: flex;
    justify-content: space-between;
    margin-bottom: 10px;
    width: 100%;
}

.price-status p {
    margin: 0;
    font-size: 16px;
}

.item-box-info p {
    margin: 5px 0;
    font-size: 14px;
}

.item-box-info {
    min-height: 180px;
}

.share, .other {
    display: flex;
    align-items: center;
    justify-content: center;
    margin-top: 30px;
}

.other {
    margin-top: 20px;
}

button.active-item-other {
    background: white;
    color: #6558f5;
    text-decoration: underline;
}

/* Aplica o blur apenas no conteúdo do 4º item */
.list-item .item-box.blur {
    filter: blur(5px);  /* Aplica o efeito de blur no conteúdo */
    opacity: 0.6; /* Reduz opacidade para enfatizar o efeito */
    pointer-events: none; /* Desativa interação com o item */
    position: relative; /* Necessário para a sobreposição do texto */
}

/* Estilização do texto sobre o 4º item com blur */
.list-item .blur-overlay {
    position: relative;
    bottom: 20rem;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 15px;
    background-color: rgb(255 255 255 / 91%);
    font-size: 18px;
    font-weight: bold;
    color: #333;
    border-radius: 50px;
    z-index: 10;
    pointer-events: none;
    text-align: center;
}

